/*==========Google Fonts==========*/
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

/*==========Reset==========*/
*, *::before, *::after {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    outline: none;
    text-decoration: none;
    list-style: none;
    font-family: 'Poppins', sans-serif;
}

/*==========Root==========*/
:root {

  --red-color: hsl(349, 98%, 58%);
  --blue-color: hsl(192, 98%, 47%);
  --dark-color: hsl(0, 0%, 13%);
  --white-color: hsl(0, 0%, 100%);
  --transparent-color: hsla(0, 0%, 0%, 0);
  --transition: all 400ms ease;
  --bg-color: hsl(218, 29%, 95%);
  --glass-color: hsla(0, 0%, 100%, 0.075);
  --shadow: 0 1px 4px hsla(0, 0%, 0%, 0.2);
  --shadow2: 0 20px 25px -5px rgba(0, 0, 0, 0.055), 0 8px 10px -6px rgb(0 0 0 / 0.1);

}